import '../sass/main.scss';

(() => {
  document.addEventListener('DOMContentLoaded', () => {
    const toggles = [...document.querySelectorAll('.grid__box-totals--toggle')];
    toggles.forEach(el => {
      el.addEventListener('click', () => {
        el.classList.toggle('grid__box-totals--opened');
      });
    });
  });
})();
